define([], function () {

    const CaseInfo = {
        contentCard: function (data, type, idx, active = false, otherAttributes = false) {
            const otherAttributesHtml = `
                                        <div class="otherAttributtesCtnClsCm">
                                            <div class="attributteCtnClsCm">
                                                <label for="resolutionTypeName">Tipo Doc. Sustento:</label>
                                                <input type="text" id="resolutionTypeName" name="resolutionTypeName" value="${data.resolutionTypeName || '---'}" readonly>
                                            </div>
                                            <div class="attributteCtnClsCm">
                                                <label for="resolutionDocument">Nro. Doc. Sustento:</label>
                                                <input type="text" id="resolutionDocument" name="resolutionDocument" value="${data.resolutionDocument || '---'}" readonly>
                                            </div>
                                        </div>   
                                        <div class="otherAttributtesCtnClsCm">
                                            <div class="attributteCtnClsCm">
                                                <label for="floor">Piso:</label>
                                                <input type="text" id="floor" name="floor" value="${data.floor || '---'}" readonly>
                                            </div>
                                            <div class="attributteCtnClsCm">
                                                <label for="municipalNumber">Número municipal:</label>
                                                <input type="text" id="municipalNumber" name="municipalNumber" value="${data.municipalNumber || '---'}" readonly>
                                            </div>
                                        </div>                                     
                                        `
            const cardHtml = `<div class="caseInfoClsCm">
                                <div class="headPredInfoClsCm ${type === 'original' ? 'headPredInfoOriginalClsCm' : 'headPredInfoResultClsCm'}">
                                    <span class="alignVCenter">Predio: ${data.cup || idx}</span>
                                    <button class="zoomPredInfoClsCm ${type === 'original' ? 'active' : null} toolsPredInfoClsCm" data-cup=${data.cup}>
                                        <i class="fa fa-search" style="width: 16px; height: 16px"></i>
                                    </button>
                                    <button class="colapsePredInfoClsCm toolsPredInfoClsCm">
                                        <i class='fas fa-angle-down' style="width: 16px; height: 16px"></i>
                                    </button>
                                </div>
                                <div class="bodyPredInfoClsCm ${active ? 'active' : null}">
                                    <label for="direccion">Dirección:</label>
                                    <input type="text" id="direccion" name="direccion" value="${data.address.replace("None", '')}" readonly>
                                    ${otherAttributes ? otherAttributesHtml : ''}
                                </div>
                            </div>`;
            return cardHtml;
        },
        // Otras funciones o propiedades
    };

    return CaseInfo;
});